/* seccion testimonios */
.testimony,.work-approach,.get-involved{
    margin: 4rem 0rem 4rem 0rem;
    font-size: 20px;
}

.testimony > div >div {
    margin-bottom: 10px;
}
.testimony > div >div > p{
    margin: 0px 0px 15px 0px;
}
.img-home{
    width: 100%;
    border-radius: 8px;
}

.img-container{
    text-align: center;
}
.div-read-more{
    display: flex;
    justify-content: flex-end;
}

.a-read-more{
    margin-right: 50px;
    font-size: 15px;
    color: #7E2C81;
    border-bottom: 3px solid  #7E2C81;
    padding-bottom: 3px;
}
.a-read-more:hover{
    text-decoration: none;
    color:rgb(154, 101, 156);
    border-bottom: 3px solid  rgb(154, 101, 156);
}
/* seccion 'enfoque de trabajo' */
.carousel-indicators li {
    width: 30px !important;
    height: 30px !important;
    border-radius: 100% !important;
    background-color: white !important;
    border: 1px solid #7E2C81 !important;
}
.carousel-indicators .active{
    background-color: #7E2C81 !important;
    border:none;
}
.carousel-indicators {
    margin-bottom: -50px;
}

.carousel-title,.testimony-title,.work-approach-title,.get-involved-title{
    font-weight: bolder;
    margin-bottom: 24px;
}
.carousel-title{
    margin-bottom: 5px;
    font-size:25px;
    font-weight: bolder;
    color: #6c1a6f;
}

.w-100{
    margin: 6px 0px 6px 0px;
    border-radius: 8px;
}


/*/ seccion involucrate /*/ 
.get-involved{
    margin:8rem 0rem 6rem 0rem;
}

.button{
    text-decoration: none;
